"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { FeedbackModal } from "@/components/modals/feedback-modal"
import { authService, type TeamDetails } from "@/lib/auth-service"
import { ArrowRight, Users, BookOpen, Award, Mail, Loader } from "lucide-react"

export default function ConfirmTeamPage() {
  const router = useRouter()
  const [team, setTeam] = useState<TeamDetails | null>(null)
  const [loading, setLoading] = useState(true)
  const [confirming, setConfirming] = useState(false)
  const [feedback, setFeedback] = useState<{ type: "success" | "error"; title: string; message: string } | null>(null)

  useEffect(() => {
    const fetchTeamDetails = async () => {
      try {
        const teamData = await authService.getTeamDetails(true)
        setTeam(teamData)
      } catch (err: any) {
        setFeedback({ type: "error", title: "Error", message: err.message || "Failed to load team details" })
      } finally {
        setLoading(false)
      }
    }

    fetchTeamDetails()
  }, [])

  const handleConfirm = async () => {
    setConfirming(true)

    try {
      await authService.confirmTeamDetails()
      setFeedback({ type: "success", title: "Success", message: "Team details confirmed! Redirecting..." })
      setTimeout(() => router.push("/dashboard"), 1500)
    } catch (err: any) {
      setFeedback({ type: "error", title: "Error", message: err.message || "Failed to confirm team details" })
    } finally {
      setConfirming(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center px-4">
        <div className="text-center">
          <Loader className="w-12 h-12 text-accent animate-spin mx-auto mb-4" />
          <p className="text-muted-foreground font-medium">Loading team details...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <div className="bg-card border-b border-border shadow-sm sticky top-0 z-10">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between gap-4">
            <img src="/images/gitam-logo-20-282-29.png" alt="GITAM" className="h-12 object-contain" />
            <h1 className="text-xl font-bold sih-heading">Team Confirmation</h1>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <main className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="space-y-6">
          <div className="text-center mb-8">
            <h2 className="text-3xl md:text-4xl font-bold sih-heading mb-2">Confirm Team Details</h2>
            <p className="text-muted-foreground">
              Please verify your team information before proceeding to the dashboard
            </p>
          </div>

          {team && (
            <>
              {/* Team Information Card */}
              <Card className="p-6 md:p-8 shadow-md border-border">
                <h3 className="text-xl font-bold sih-heading mb-6 flex items-center gap-3">
                  <Award className="w-6 h-6 text-accent" />
                  Team Information
                </h3>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 md:gap-6">
                  <div className="p-4 rounded-lg bg-primary/5 border border-primary/20">
                    <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1">Team Name</p>
                    <p className="text-lg font-semibold text-foreground">{team.name}</p>
                  </div>

                  <div className="p-4 rounded-lg bg-secondary/5 border border-secondary/20">
                    <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1">
                      Institution
                    </p>
                    <p className="text-lg font-semibold text-foreground">{team.institution}</p>
                  </div>

                  {team.problem_statement && (
                    <div className="p-4 rounded-lg bg-accent/5 border border-accent/20 md:col-span-2">
                      <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1 flex items-center gap-2">
                        <BookOpen className="w-4 h-4" />
                        Problem Statement
                      </p>
                      <p className="text-lg font-semibold text-foreground">{team.problem_statement.title}</p>
                      <p className="text-sm text-accent mt-2 font-medium">ID: {team.problem_statement.id}</p>
                    </div>
                  )}

                  {team.faculty_mentor && (
                    <div className="p-4 rounded-lg bg-primary/5 border border-primary/20">
                      <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-1">
                        Faculty Mentor
                      </p>
                      <p className="text-lg font-semibold text-foreground">{team.faculty_mentor.name}</p>
                    </div>
                  )}
                </div>
              </Card>

              {/* Team Members Card */}
              <Card className="p-6 md:p-8 shadow-md border-border">
                <h3 className="text-xl font-bold sih-heading mb-6 flex items-center gap-3">
                  <Users className="w-6 h-6 text-accent" />
                  Team Members <span className="ml-2 text-accent">({team.members?.length || 0})</span>
                </h3>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {team.members?.map((member, idx) => (
                    <div
                      key={idx}
                      className="p-4 rounded-lg border border-border hover:border-accent/50 card-hover bg-card"
                    >
                      <div className="flex items-start justify-between gap-3">
                        <div className="flex-1">
                          <p className="font-semibold text-foreground">{member.user.name}</p>
                          <p className="text-sm text-muted-foreground flex items-center gap-2 mt-1">
                            <Mail className="w-4 h-4" />
                            {member.user.email}
                          </p>
                        </div>
                        <span className="text-xs font-semibold px-3 py-1 rounded-full bg-accent/10 text-accent uppercase tracking-wide">
                          {member.role}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </Card>

              {/* Confirmation Actions Card */}
              <Card className="p-6 md:p-8 shadow-md border-border bg-primary/2">
                <div className="space-y-6">
                  <div className="p-4 rounded-lg bg-secondary/10 border border-secondary/30">
                    <p className="text-sm text-foreground font-medium">
                      âœ“ Please review all information above carefully. Once confirmed, you will be redirected to your
                      dashboard.
                    </p>
                  </div>

                  <div className="flex flex-col sm:flex-row gap-3">
                    <Button
                      onClick={handleConfirm}
                      disabled={confirming}
                      className="button-primary flex-1 py-3 font-semibold"
                    >
                      {confirming ? "Confirming..." : "Confirm & Continue"}
                      {!confirming && <ArrowRight className="w-4 h-4 ml-2" />}
                    </Button>
                    <Button onClick={() => router.push("/")} className="button-secondary flex-1 py-3 font-semibold">
                      Cancel
                    </Button>
                  </div>
                </div>
              </Card>
            </>
          )}
        </div>
      </main>

      {/* Feedback Modal */}
      <FeedbackModal
        open={!!feedback}
        type={feedback?.type || "info"}
        title={feedback?.title || ""}
        message={feedback?.message || ""}
        onClose={() => setFeedback(null)}
        autoClose={feedback?.type === "success"}
      />
    </div>
  )
}
